@import url('https://fonts.googleapis.com/css?family=Montserrat');

.outer {
  border: solid 2px #000;
  padding: 5px !important;
  margin-top: 0.8rem;
}

.card button {
  width: 100% !important;
  height: 100% !important;
  margin: 5px 0 !important;
}

body {
  height: 100vh;
  font-family: 'Montserrat', sans-serif;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#c6fff3+0,e5c6ff+100 */
  background: rgb(198,255,243);  
  background: -moz-linear-gradient(-45deg,  rgba(198,255,243,1) 0%, rgba(229,198,255,1) 100%); 
  background: -webkit-linear-gradient(-45deg,  rgba(198,255,243,1) 0%,rgba(229,198,255,1) 100%); 
  background: linear-gradient(135deg,  rgba(198,255,243,1) 0%,rgba(229,198,255,1) 100%); 
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c6fff3', endColorstr='#e5c6ff',GradientType=1 ); 
}

#no-border-button {
  border-radius: 0 !important;
  border: 0 !important;
  padding:0 !important;
  margin: 0 !important;
  height: 100%;
  width: 100%;
}


#submit-button {
  border-radius: 0 !important;
  margin-top: 10px;
  font-size: 1rem;
  font-weight: 400;
}

#real-brand {
  font-weight: 600 !important;
}

input {
  border-radius: 0 !important;
}

